import Search from '@/components/header/header.vue';
import acquireString from '@/common/commonFunction.js';
import {
	onlineURL
} from '@/common/common.js';
import {
	get,
	post
} from '@/common/methods.js';
export default {
	data() {
		return {
			message: '',
			randomString: '',
			registerPhoneId: '',
			verifyNumber: '',
			phoneNumber: '',
			password: '',
			code: '',
			reqStatus: '',
			countdown: '获取验证码',
			disabled: false,
			timestatus: false,
			timestatus2: true,
			clear: '',
			verifyStatus: '',
			phoneStatus: '',
			registerStatus: '',
			countown_style: {
				zIndex: ''
			}
		}
	},
	onLoad() {
		this.randomString = acquireString.randomWord(false, 4);
		console.log(this.randomString);
	},
	methods: {
		login() {
			uni.reLaunch({
				url: './phoneLogin'
			});
		},
		getCode() {

			let regPhone = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/;
			if (regPhone.test(this.phoneNumber)) {
				// get('/auth/register', {
				// 	'phone': this.phoneNumber,
				// 	'randomStr': this.randomString
				// }).then(res => {
				// 	console.log(res)
				// 	// let status = res.data.status.split('-')[1];
				// 	// if (status == 'SUCCESS') {
				// 	// 	this.reqStatus = true;
				// 	// } else {
				// 	// 	this.reqStatus = false;
				// 	// }
				// });
				uni.request({
					url: onlineURL + '/auth/register?phone=' + this.phoneNumber + '&&randomStr=' + this.randomString,
					method: 'GET',
					success: res => {
						console.log(res)
				
						this.phoneStatus = res.data.status.split('-')[1];
						console.log(this.phoneStatus)
							if (this.phoneStatus == 'SUCCESS') {
								this.reqStatus = true;
							} else {
								this.reqStatus = false;
							}
					}
				});
				
				setTimeout(() => {
					if (this.reqStatus == true) {
						this.countdown = 60;
						this.timestatus2 = false;
						this.timestatus = true;
						this.clear = setInterval(this.countDown, 1000);
						this.countown_style.zIndex = -1;
						uni.request({
							url: onlineURL + '/code/phone/register?randomStr=' + this.randomString + '&&phone=' + this.phoneNumber,
							method: 'GET',
							success: res => {
								console.log(res);
								if (res.data.message == '验证码已发送') {
									uni.showToast({
										title: '发送成功',
										duration: 2000,
										icon: 'success',
									});
									console.log('获取验证码成功');
								} else {
									console.log('获取验证码失败');
								}
							}
						});
					} else {
						uni.showToast({
							title: '手机号码已被使用',
							duration: 2000,
							icon: 'none',
						});
					}
				}, 1000);

			} else {
				uni.showToast({
					title: '请输入正确的手机号码',
					duration: 2000,
					icon: 'none',
				});
				return false;
			}


		},
		countDown() {
			if (!this.countdown) {
				this.disabled = false;
				this.timestatus = false;
				this.countdown = '获取验证码';

				clearInterval(this.clear);
				this.timestatus2 = true;
				this.countdown = '重新获取';
				this.countown_style.zIndex = 2;
			} else {
				--this.countdown;
			}
		},
		// 验证码倒计时 end 
		// 注册
		registerID() {

			uni.request({
				url: onlineURL + '/auth/register?phone=' + this.phoneNumber + '&&randomStr=' + this.randomString,
				method: 'GET',
				success: res => {
					console.log(res)

					this.phoneStatus = res.data.status.split('-')[1];
					console.log(this.phoneStatus)
				}
			});
			uni.request({
				url: onlineURL + '/auth/register?code=' + this.verifyNumber + '&&randomStr=' + this.randomString,
				method: 'GET',
				success: res => {
					console.log(res)
					this.verifyStatus = res.data.status.split('-')[1];
					console.log(this.verifyStatus)
				}
			});
			setTimeout(() => {
				//console.log(this)
				console.log(this.phoneStatus)
				if (this.phoneStatus == 'FAILED') {
					uni.showToast({
						title: '手机号码已被使用',
						duration: 2000,
						icon: 'none',
					});
					return false;
				} else if (this.phoneStatus == 'ERROR') {
					uni.showToast({
						title: '手机号码格式不正确',
						duration: 2000,
						icon: 'none',
					});
					return false;
				} else if (this.verifyStatus == 'FAILED') {
					uni.showToast({
						title: '验证码错误',
						duration: 2000,
						icon: 'none',
					});
					return false;
				} else if (this.verifyStatus == 'ERROR') {
					uni.showToast({
						title: '请输入验证码',
						duration: 2000,
						icon: 'none',
					});
					return false;
				} else {
					let regPwd = /^[a-z0-9A-Z]{6,14}$/;
					if (regPwd.test(this.password)) {
						uni.request({
							url: onlineURL + '/auth/register?phone=' + this.phoneNumber + '&&randomStr=' + this.randomString +
								'&&password=' + this.password,
							method: 'GET',
							success: res => {
								// let register = res.data.status;
								// this.registerStatus = register.split('-')[1];
								uni.showToast({
									title: '注册成功',
									duration: 2000,
									icon: 'success',
								});
								setTimeout(() => {
									uni.reLaunch({
										url: '../index/index/index'
									});
								}, 1000);
							}
						});
					} else {
						uni.showToast({
							title: '请输入正确的密码格式',
							duration: 2000,
							icon: 'none',
						});
					}


				}
			}, 1000)
		}
	},
	components: {
		Search
	}
}
